import React from 'react'
import PropTypes from 'prop-types'
import {Bar} from 'react-chartjs-2'

import colors from '../styles/colors'

const options = {
  tooltips: {
    mode: 'index'
  },
  scales: {
    xAxes: [{
      stacked: true
    }],
    yAxes: [{
      display: true,
      stacked: true,
      position: 'left',
      id: 'y-axis-1'
    }, {
      display: true,
      position: 'right',
      id: 'y-axis-2',
      gridLines: {
        drawOnChartArea: false
      }
    }]
  }
}

const formatData = data => {
  const datasets = []

  if (data.some(h => h.casConfirmes)) {
    datasets.push({
      label: 'En vie',
      data: data.map(h => h.casConfirmes - (h.deces || 0)),
      backgroundColor: colors.orange,
      yAxisID: 'y-axis-1',
      order: 2
    })
  }

  if (data.some(h => h.deces)) {
    datasets.push({
      label: 'Décédés',
      data: data.map(h => h.deces),
      backgroundColor: colors.red,
      yAxisID: 'y-axis-1',
      order: 2
    })
  }

  datasets.push({
    type: 'line',
    fill: false,
    label: 'Nouveaux cas',
    data: data.map((h, i) => {
      if (i === 0) {
        return 0
      }

      const casConfirmesVeille = data[i - 1].casConfirmes
      return casConfirmesVeille ? h.casConfirmes - casConfirmesVeille : 0
    }),
    backgroundColor: colors.darkGrey,
    yAxisID: 'y-axis-2',
    order: 1
  })

  return {
    labels: data.map(h => h.date),
    datasets
  }
}

const ConfirmedChart = ({data, height}) => (
  <div style={{padding: '1em'}}>
    <Bar data={formatData(data)} options={options} height={height} />
  </div>
)

ConfirmedChart.defaultProps = {
  height: null
}

ConfirmedChart.propTypes = {
  data: PropTypes.array.isRequired,
  height: PropTypes.number
}

export default ConfirmedChart
